@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  background-color: #f2f2f2;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .page__body {
    width: 1200px;
    margin: 0 auto;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.button {
  display: block;
  box-sizing: border-box;
  font-family: inherit;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:focus-visible {
  outline: 3px solid #23a9e1;
  outline-offset: 2px;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled,
.button.disabled {
  background-color: #e5e5e5;
}
.main-header {
  position: relative;
  min-height: 56px;
  background-color: #ffffff;
}
.main-header__wrapper {
  box-sizing: border-box;
}
@media (min-width: 1200px) {
  .main-header__wrapper {
    max-width: 1200px;
  }
}
.main-header__logo {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  width: 185px;
  height: 96px;
  margin: 0 auto;
}
.main-header__logo:focus-visible {
  outline: 3px solid #23a9e1;
  outline-offset: 2px;
}
@media (min-width: 1200px) {
  .main-header__logo-image {
    width: 185px;
    height: 96px;
  }
}
.main-nav {
  position: relative;
}
@media (max-width: 767px) {
  .main-nav--nojs {
    position: relative;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
  }
  .main-nav--open {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2;
  }
  .main-nav--open::after,
  .main-nav--nojs::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 153px;
  }
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    column-gap: 240px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 12px;
  right: 17px;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/icons-toggle.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:focus-visible {
  outline: 3px solid #23a9e1;
  outline-offset: 2px;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--open .main-nav__toggle {
  background-image: none;
}
.main-nav--open .main-nav__toggle::before,
.main-nav--open .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 16px;
  right: 0;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav--open .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--open .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--open .main-nav__toggle:active::before,
.main-nav--open .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list,
  .main-nav--open .main-nav__list {
    display: flex;
    flex-direction: column;
    background-color: #81b3d2;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    width: 153px;
    padding: 17px;
    box-sizing: border-box;
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    width: 240px;
  }
}
.main-nav__link {
  display: block;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
}
@media (max-width: 767px) {
  .main-nav__link {
    padding: 17px 50px 12px;
    border-bottom: 1px solid rgba(1, 1, 1, 0.2);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .main-nav__link--no-border {
    box-shadow: none;
    border-bottom: none;
  }
  .main-nav__link[href]:hover {
    background-color: #669ec0;
  }
  .main-nav__link[href]:focus-visible {
    outline: 4px solid #23a9e1;
    outline-offset: -3px;
  }
  .main-nav__link[href]:active {
    color: rgba(255, 255, 255, 0.3);
    background-color: #5496bd;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
  .main-nav__link--current {
    color: #766357;
  }
  .main-nav__link[href]:hover {
    color: #81b3d2;
  }
  .main-nav__link[href]:focus-visible {
    outline: 3px solid #23a9e1;
    outline-offset: 5px;
  }
  .main-nav__link[href]:active {
    color: rgba(0, 0, 0, 0.3);
  }
}
